#intro {
  z-index: 2;
  opacity: 1;
  background: none;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  transition: opacity .5s;
  display: flex;
  position: relative;
  overflow: hidden;
}

#intro-content {
  z-index: 5;
  opacity: 1;
  background-color: #161616;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: 50vh;
  margin-top: auto;
  display: flex;
  position: relative;
}

#introText {
  pointer-events: none;
  opacity: 0;
  z-index: 6;
  color: #fff;
  text-align: center;
  font-family: Orbitron, sans-serif;
  transition: opacity 4s;
}

#title {
  pointer-events: none;
  z-index: 10;
  width: 100vw;
  position: absolute;
  top: 48%;
}

#instruction {
  opacity: 0;
  -webkit-user-select: none;
  user-select: none;
  caret-color: #0000;
  margin-top: 2rem;
  transition: opacity 4s;
}

#enterSystem {
  opacity: 0;
  pointer-events: none;
  color: #fff;
  letter-spacing: 1px;
  cursor: pointer;
  backdrop-filter: blur(6px);
  -webkit-user-select: none;
  user-select: none;
  caret-color: #0000;
  background-color: #ffffff1a;
  border: 2px solid #fff;
  border-radius: 30px;
  margin-top: 7rem;
  padding: .8rem 2rem;
  font-family: Orbitron, sans-serif;
  font-size: 1.2rem;
  font-weight: 500;
  transition: opacity 4s;
  box-shadow: 0 0 15px #ffffff1a;
}

#enterSystem:hover {
  background-color: #ffffff4d;
  transform: scale(1.05);
  box-shadow: 0 0 20px #fff6;
}

#enterSystem:active {
  transform: scale(.98);
  box-shadow: 0 0 10px #fff9;
}
